<template>
<div>
  <div class="uk-article"> 
    <h3 class="uk-article-title">{{ book.title }}</h3> 
    <p class="uk-article-meta">{{ book.house }} - {{book.source}}</p> 
    <br>
  </div>
  <div>
    <form>
      <input type="radio" name="choice" id="paragraph" checked="checked" v-on:mousedown="chooseDisplay('para')">
      <label>In Paragraph</label>
      <input type="radio" name="choice" id="list" v-on:mousedown="chooseDisplay('list')">
      <label>In List (default: 3 items)</label>
    </form>
    <div><span>OR</span></div>
    <label>Word Count (1 to 411)</label>
    <input type="text" id="word" v-model="countVal">
    <input class="uk-button uk-button-default uk-button-small" type="button" name="choiceCount" value="y"id="count" v-on:mousedown="chooseDisplay('count')">
  </div>
  <div>
    <textarea rows="10" cols="80" id="content" >{{ displaying }}</textarea>
    <br>
    <button class="uk-button uk-button-primary" v-on:mousedown="copyContent()">Copy</button>
  </div>
</div>
</template>

<script>
export default {
  name: 'Book',
  data () {
    return {
      book: {
        title: '‰πùÈò¥ÁúüÁªè',
        house: 'unknown',
        source: 'Â∞ÑÈõïËã±ÈõÑ‰º†',
        content: 'Â§©‰πãÈÅìÔºåÊçüÊúâ‰ΩôËÄåË°•‰∏çË∂≥ÔºåÊòØÊïÖËôöËÉúÂÆûÔºå‰∏çË∂≥ËÉúÊúâ‰Ωô„ÄÇÂÖ∂ÊÑèÂçöÔºåÂÖ∂ÁêÜÂ••ÔºåÂÖ∂Ë∂£Ê∑±ÔºåÂ§©Âú∞‰πãË±°ÂàÜÔºåÈò¥Èò≥‰πãÂÄôÂàóÔºåÂèòÂåñ‰πãÁî±Ë°®ÔºåÊ≠ªÁîü‰πãÂÖÜÂΩ∞Ôºå‰∏çË∞ãËÄåÈÅóËøπËá™ÂêåÔºåÂãøÁ∫¶ËÄåÂπΩÊòéÊñØÂ•ëÔºåÁ®ΩÂÖ∂Ë®ÄÊúâÂæÆÔºåÈ™å‰πã‰∫ã‰∏çÂøíÔºåËØöÂèØË∞ìËá≥ÈÅì‰πãÂÆóÔºåÂ•âÁîü‰πãÂßãÁü£„ÄÇÂÅáËã•Â§©Êú∫ËøÖÂèëÔºåÂ¶ôËØÜÁéÑÈÄöÔºåÊàêË∞ãËôΩÂ±û‰πéÁîüÁü•ÔºåÊ†áÊ†º‰∫¶ËµÑ‰∫éÊ≤ªËÆ≠ÔºåÊú™Â∞ùÊúâË°å‰∏çÁî±ÈÄÅÔºåÂá∫‰∏çÁî±‰∫ßËÄÖ‰∫¶„ÄÇÁÑ∂ÂàªÊÑèÁ†îÁ≤æÔºåÊé¢ÂæÆÁ¥¢ÈöêÔºåÊàñËØÜÂ•ëÁúüË¶ÅÔºåÂàôÁõÆÁâõÊó†ÂÖ®ÔºåÊïÖÂä®ÂàôÊúâÊàêÔºåÁäπÈ¨ºÁ•ûÂπΩËµûÔºåËÄåÂëΩ‰∏ñÂ•áÊù∞ÔºåÊó∂Êó∂Èó¥Âá∫ÁÑâ„ÄÇ ‰∫îËóèÂÖ≠Â∫ú‰πãÁ≤æÊ∞îÔºåÁöÜ‰∏äÊ≥®‰∫éÁõÆËÄå‰∏∫‰πãÁ≤æ„ÄÇÁ≤æ‰πãÊ°à‰∏∫ÁúºÔºåÈ™®‰πãÁ≤æ‰∏∫Áû≥Â≠êÔºåÁ≠ã‰πãÁ≤æ‰∏∫ÈªëÁúºÔºåË°Ä‰πãÁ≤æÂäõÁªúÔºåÂÖ∂Ê°àÊ∞î‰πãÁ≤æ‰∏∫ÁôΩÁúºÔºåËÇåËÇâ‰πãÁ≤æ‰∏∫Á∫¶ÊùüÔºåË£πÊí∑Á≠ãÈ™®Ë°ÄÊ∞î‰πãÁ≤æËÄå‰∏éËÑâÂπ∂‰∏∫Á≥ªÔºå‰∏äÂ±û‰∫éËÑëÔºåÂêéÂá∫‰∫éÈ°π‰∏≠„ÄÇÊïÖÈÇ™‰∏≠‰∫éÈ°πÔºåÂõ†ÈÄ¢ÂÖ∂Ë∫´‰πãËôöÔºåÂÖ∂‰∫∫Ê∑±ÔºåÂàôÈöèÁúºÁ≥ª‰ª•ÂÖ•‰∫éËÑëÔºåÂÖ•ÊâãËÖºÂàôËÑëËΩ¨ÔºåËÑëËΩ¨ÂàôÂºïÁõÆÁ≥ªÊÄ•ÔºåÁõÆÁ≥ªÊÄ•ÂàôÁõÆÁú©‰ª•ËΩ¨Áü£„ÄÇÈÇ™ÂÖ∂Á≤æÔºåÂÖ∂Á≤æÊâÄ‰∏≠‰∏çÁõ∏ÊØî‰∫¶ÂàôÁ≤æÊï£ÔºåÁ≤æÊï£ÂàôËßÜÂ≤êÔºåËßÜÂ≤êËßÅ‰∏§Áâ©„ÄÇ Èò¥ÊûÅÂú®ÂÖ≠Ôºå‰Ωï‰ª•Ë®Ä‰πù„ÄÇÂ§™ÊûÅÁîü‰∏§‰ª™ÔºåÂ§©Âú∞ÂàùÂà®Âà§„ÄÇÂÖ≠Èò¥Â∑≤ÊûÅÔºåÈÄ¢‰∏ÉÂΩíÂÖÉÂ§™Á¥†ÔºåÂ§™Á¥†Ë•øÊñπÈáëÂæ∑ÔºåÈò¥‰πãÊ∏ÖÁ∫ØÔºåÂØí‰πãÊ∏äÊ∫ê„ÄÇ'
      },
      countVal: 0,
      listCount: 3,
      displaying: ''
    }
  },
  created: function () {
    this.displaying = this.book.content
  },
  methods: {
    copyContent: function () {
      var content = document.querySelector('#content')
      content.select()
      var successful = document.execCommand('copy')
      var message = successful ? 'success' : 'failed'
      console.log('copy text is ' + message)
      console.log(successful)
    },
    chooseDisplay: function (displayStyle) {
      if (displayStyle === 'list') {
        var tmp = ''
        for (var i = 0; i < this.listCount - 1; i++) {
          tmp = tmp.concat(tmp, '<li>' + this.book.content.split('„ÄÇ')[i] + '</li>')
        }
        this.displaying = tmp
      } else if (displayStyle === 'count') {
        console.log(this.countVal)
        if (this.countVal < 0 || this.countVal > 411) {
          this.displaying = this.book.content
        } else {
          this.displaying = this.book.content.substring(0, this.countVal)
        }
      } else {
        this.displaying = this.book.content
        console.log(this.displaying.length)
      }
    }
  }
}
</script>

